footer {
    background-color: var(--azulMar);
    color: var(--negro);
    padding: 30px 35px;
    display: flex;
    align-items: center;
    box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
    height: 95px;
}

footer {
margin-top: auto;
display: flex;
}

.footer--title {
color: var(--orange);
font-size: 2.2em;
margin: 0 0 5px 0;
}

.footer--eslogan{
margin: 0;
}

.footer--kayu {
margin-right: auto;
}

.footer--privacy>h3 {
margin: 0;
padding: 10px 0px;
cursor: pointer;
}

.footer--privacy>h3:hover {
text-decoration: underline;
}